import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import Mutation from '../components/Mutation';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import VpnKey from '@material-ui/icons/VpnKey';
import EnhancedTable from '../components/EnhancedTable';
import EnhancedMenu, { CreateMenuItem } from '../components/EnhancedMenu';
import Query from '../components/Query';
import ChangePassword from '../components/Dialogs/ChangePassword';
import { defaultState } from '../graphql/resolvers';
import filter from '../utils/filter';
import { Routes } from '../Router';

import { USERS, USER_CREDIT_HISTORY } from '../graphql/query/remote';
import { USER_ENTRY_TABLE as USER_ENTRY_TABLE_QUERY } from '../graphql/query/local';

import { USER_ENTRY_TABLE } from '../graphql/mutation/local';
import { DELETE_USERS } from '../graphql/mutation/remote';

import commanRender from '../utils/commanRender';
import { RENDER_VALUE } from '../utils/commanRender';

const getContactJSX = (value) => (
  value ?
    <Link
      to={""}
      onClick={e =>
        setTimeout(e => window.location.href = `tel:+91-${value}`, 0)
      }
    >
      {value}
    </Link> :
    "-"
)

const styles = theme => ({
  root: {
    width: '100%'
  },
  fab: {
    position: 'fixed',
    width: "4rem",
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  }
});

export const userColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'username', numeric: false, disablePadding: false, colSpan: 1, label: 'Username' },
  { id: 'contact', numeric: false, disablePadding: false, colSpan: 1, label: 'Phone Number' },
  { id: 'siteCount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Sites' },
  { id: 'totalSitesCost', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Sites Cost (₹)' },
  { id: 'totalReceivedAmount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Received Amount (₹)' },
  { id: 'spent', numeric: false, disablePadding: false, colSpan: 1, label: 'Spent Amount (₹)' },
  { id: 'balance', numeric: false, disablePadding: false, colSpan: 1, label: 'Balance Amount (₹)' },
];
export const userExclude = ["id", "count", "sites", "createdAt", "updatedAt", "__typename"];

class User extends PureComponent {
  render() {
    const { classes, history, ...restProps } = this.props;
    return (
      <div>
        <Grid container className={classes.root}>
          <Grid item xs={12}>
            <Mutation mutation={USER_ENTRY_TABLE}>
              {(lmutate, args) => {
                return (
                  <Query query={USER_ENTRY_TABLE_QUERY}>
                    {({ loading, error, data: { userEntryTable } }) => {
                      const { page, rowsPerPage } = userEntryTable;
                      return (
                        <Mutation mutation={DELETE_USERS}>
                          {(deleteUsers, { loading: dloading }) => {
                            return (
                              <Query query={USERS} variables={{ limit: rowsPerPage, skip: page * rowsPerPage }}>
                                {
                                  ({ loading, data, refetch }) => {
                                    // refetch();                              
                                    return (
                                      <EnhancedTable
                                        {...restProps}
                                        loading={loading || dloading}
                                        data={data.users}
                                        title="Users"
                                        settings={userEntryTable}
                                        count={(data.users.length && data.users[0].count) || data.users.length}
                                        columnData={userColumnData}
                                        exclude={userExclude}
                                        historyable={USER_CREDIT_HISTORY}
                                        menu={
                                          (selected) => {
                                            return selected.length === 1 ?
                                              (
                                                <EnhancedMenu
                                                  options={[
                                                    (props) => {
                                                      return (
                                                        <ChangePassword
                                                          {...props}
                                                          reset
                                                          positiveHandler={(fn, args) => {
                                                            return fn({
                                                              variables: {
                                                                id: selected[0],
                                                                data: filter(args, ["confirmNewPassword"])
                                                              }
                                                            })
                                                          }}>
                                                          <CreateMenuItem
                                                            {...props}
                                                            icon={<VpnKey />}
                                                            content="Reset Password"
                                                            tooltip="Reset Password"
                                                          />
                                                        </ChangePassword>
                                                      )
                                                    }
                                                  ]}
                                                />
                                              ) : (null);
                                          }}
                                        options={{
                                          onCredit: async (fn, args) => {
                                            await fn({ variables: args });
                                            // refetch();
                                          },
                                          onDelete: async args => {
                                            console.log("calling deleteMutate function...");
                                            await deleteUsers({ variables: args });
                                            await lmutate({ variables: defaultState.userEntryTable });
                                            refetch();
                                          },
                                          onAdd: () => history.push(Routes.User.create())
                                        }}
                                        render={commanRender({
                                          match: "spent",
                                          renderValue: ({ key, value }) => {
                                            if (key === 'contact') {
                                              return { value: getContactJSX(value) };
                                            }
                                            return RENDER_VALUE({ key, value });
                                          }
                                        })}
                                        mutate={async args => {
                                          await lmutate({ variables: args });
                                          // refetch();
                                        }}>
                                      </EnhancedTable>
                                    );
                                  }
                                }
                              </Query>
                            );
                          }}
                        </Mutation>
                      );
                    }}
                  </Query>
                );
              }}
            </Mutation>
          </Grid>
        </Grid>
        {/* <Grid container justify="space-between" className={classes.fab}>
          <Grid item>
            <Button variant="fab" color="primary" component={Link} to={Routes.User.create({ edit: false })}><UserAddIcon /></Button>
          </Grid>
        </Grid> */}
      </div>
    );
  }
}

export default withStyles(styles)(User);



