import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { ENHANCED_TABS } from '../graphql/mutation/local';
import { ENHANCED_TABS as ENHANCED_TABS_QUERY } from '../graphql/query/local';
import Query from '../components/Query';
import Mutation from '../components/Mutation';

function TabContainer({ children, dir }) {
  return (
    <Typography component="div" dir={dir} >
      {children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
  dir: PropTypes.string.isRequired,
};

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: '100%',
  },
  app: {
    marginTop: theme.spacing.unit * 8
  },
  content: {
    marginTop: theme.spacing.unit * 5
  }
});

class EnhancedTabs extends React.Component {
  // state = {
  //   value: 1,
  // };

  // handleChange = (event, value) => {
  //   this.setState({ value });
  // };

  // handleChangeIndex = index => {
  //   this.setState({ value: index });
  // };

  render() {
    const { classes, theme } = this.props;
    const { defaults } = this.props;
    const result = { tabs: [], containers: [] };
    defaults.map(e => {
      result.tabs.push(<Tab label={e.tab} key={e.tab} />);
      result.containers.push(<TabContainer dir={theme.direction} key={e.tab + '_container'}>{e.container}</TabContainer>);
      return null;
    });

    return (
      <Mutation mutation={ENHANCED_TABS}>
        {(lmutate) => {
          return (
            <Query query={ENHANCED_TABS_QUERY}>
              {({ data: { enhancedTabs } }) => {
                return (
                  <div className={classes.root}>
                    <AppBar color="default" className={classes.app}>
                      <Tabs
                        value={enhancedTabs.value}
                        onChange={(_, value) => lmutate({ variables: { value } })}
                        indicatorColor="primary"
                        textColor="primary"
                      >
                        {result.tabs}
                      </Tabs>
                    </AppBar>
                    <SwipeableViews
                      className={classes.content}
                      axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                      index={enhancedTabs.value}
                      onChangeIndex={value => lmutate({ variables: { value } })}
                    >
                      {result.containers}
                    </SwipeableViews>
                  </div>
                );
              }}

            </Query>
          );
        }}
      </Mutation>
    );
  }
}

EnhancedTabs.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(EnhancedTabs);



