import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import Mutation from '../Mutation';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Form } from 'informed';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputAdornment from '@material-ui/core/InputAdornment';
import { FormHelperText } from '@material-ui/core';
import { PROPERTY_CREDIT } from '../../graphql/mutation/remote';
// import { defaults as propertyDefaults } from '../forms/CreateProperty';
import { getDate } from '../../utils/date';
import { getJSX } from '../forms/FormGenerator';

const styles = {
  progress: {
    margin: "1rem",
  }
}

const defaults = {
  amount: {
    type: "number",
    default: 0,
    startAdornment: <InputAdornment position="start">₹</InputAdornment>
  },
  nextDueDate: {
    type: 'date',
    default: getDate(),
    validate: val => {
      if (new Date(val).getTime() <= new Date(getDate()).getTime()) {
        return "Must be a future date";
      }
      return null;
    }
  }
};

class FormDialog extends React.PureComponent {
  state = {
    open: false,
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleOpen = () => {
    this.setState({ open: true });
  };

  render() {
    const { open } = this.state;
    const {
      classes,
      history,
      selected,
      negativeHandler = e => this.handleClose(),
      positiveHandler = e => null,
      postMutation = e => null
    } = this.props;
    const title = "Add Credit",
      content = "Add credit amount to this property.";
    return (
      <div>
        <Mutation mutation={PROPERTY_CREDIT}>
          {(mutate, { loading, error, data }) => {
            return (
              <React.Fragment>
                <IconButton aria-label="Credit" onClick={this.handleOpen}>
                  <AttachMoneyIcon />
                </IconButton>
                {
                  open ? (
                    <Dialog
                      fullScreen={false}
                      open={open}
                      onClose={negativeHandler}
                      aria-labelledby={title}
                    >
                      <Form
                        onSubmit={e => {
                          positiveHandler(mutate, { data: e }, selected)
                            .then((data) => {
                              if (postMutation) {
                                postMutation(data);
                                this.handleClose(e);
                              }
                              else {
                                history.goBack()
                              }
                            })
                        }}
                      >
                        <DialogTitle id={title}>{title}</DialogTitle>
                        <DialogContent>
                          <DialogContentText>
                            {content}
                          </DialogContentText>
                          {
                            getJSX(defaults)
                          }
                        </DialogContent>
                        <DialogActions>
                          {
                            loading ? <CircularProgress className={classes.progress} /> : null
                          }
                          {
                            error ? <FormHelperText error>{error.message}</FormHelperText> : null
                          }
                          <Button onClick={negativeHandler} color="primary" autoFocus>Cancel</Button>
                          <Button size="large" type="submit" disabled={loading}>Confirm</Button>
                        </DialogActions>
                      </Form>
                    </Dialog>
                  ) : null

                }

              </React.Fragment>
            );
          }}
        </Mutation>
      </div>
    );
  }
}

export default withStyles(styles)(FormDialog);


